<template>
    <div class="dialog-wrap" :class="{'dialog-wrap-show': show}" :value="value">
        <div class="dialog">
            <div class="dialog-header">
                <slot name="header"></slot>
            </div>
            <div class="dialog-section">
                <slot></slot>
            </div>
            <div class="dialog-footer">
                <div class="dialog-action-cancel" @click="handleAction('on-cancel')">
                    <span>{{cancelText}}</span>
                </div>
                <div class="dialog-action-confirm" @click="!loading && show ? handleAction('on-confirm') : undefined">
                    <svg v-if="loading" class="dialog-loading">
                        <use xlink:href="#icon-loading"></use>
                    </svg>
                    <span v-else>{{confirmText}}</span>
                </div>
            </div>
        </div>
        <div class="mask"></div>
    </div>
</template>

<script>
  export default {
    name: "my-dialog",
    props: {
      value: {
        type: Boolean,
        default: false
      },
      cancelText: {
        type: String,
        default: '取消'
      },
      confirmText: {
        type: String,
        default: '确认'
      },
      beforeClose: Function
    },
    data() {
      return {
        show: false,
        loading: false
      }
    },
    watch: {
      value(value) {
        this.show = value
      },
      show(show) {
        //配合model
        this.$emit('input', show);

        //锁定滚动
        const body = document.body || document.documentElement
        body.className = show ? 'body-fix' : ''
      }
    },
    methods: {
      handleAction(action) {
        this.$emit(action);
        //关闭前回调函数
        if(this.beforeClose) {
            this.loading = true
            this.beforeClose(action,state =>{
              if(state !== false) {
                this.show = false
              }
              this.loading = false
            })
        }else {
          this.show = false
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
    .dialog-wrap {
        opacity: 0;
        visibility: hidden;

        transition: opacity $quick-time $function, visibility $quick-time $function $quick-time;

        .dialog {
            position: fixed;
            top: 50%;
            left: 50%;
            z-index: 3;

            border-radius: 8px;

            width: 270px;

            background-color: #f7f7f7;

            transform: translate3d(-50%, -50%, 0) scale(1.185);
            transition: transform $quick-time $function;

            overflow: hidden;

            .dialog-header {
                display: flex;
                align-items: center;

                padding: 15px 15px 0 15px;

                font-size: 16px;

                color: #333;
            }
            .dialog-section {
                position: relative;

                padding: 12px 15px 13px 15px;

                @include border(bottom,rgba(0,0,0,.2));
            }
            .dialog-footer {
                display: flex;
                height: 44px;

                .dialog-action-cancel,
                .dialog-action-confirm {
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    flex: 1;

                    font-size: 16px;
                }
                .dialog-action-cancel {
                    position: relative;

                    @include border(right,rgba(0,0,0,.2));

                    color: #222;
                }
                .dialog-action-confirm {
                    color: $theme-color;
                }
                .dialog-loading {
                    width: 20px;
                    height: 20px;

                    fill: currentColor;
                    animation: circle-animate .8s linear infinite;

                    @keyframes circle-animate {
                        100% {
                            transform: rotate(360deg);
                        }
                    }
                }
            }
        }

        .mask {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 2;

            background-color: rgba(0, 0, 0, .4);
        }

        &.dialog-wrap-show {
            opacity: 1;
            visibility: visible;

            transition: opacity $quick-time $function, visibility $quick-time $function;

            .dialog {
                transform: translate3d(-50%, -50%, 0) scale(1);
            }
        }
    }
</style>