<template>
    <div id="app">
        <nav class="nav">
            <router-link class="nav-left" to="/">
                <svg class="nav-left-icon">
                    <use xlink:href="#icon-logo"></use>
                </svg>
                <span class="nav-left-title">
                    欢迎考砸的孩子
                </span>
            </router-link>
            <div class="nav-right">
                <svg class="nav-right-icon" @click="clickLogin" v-if="JSON.stringify(this.$store.state.user)==='{}'">
                    <use xlink:href="#icon-login"></use>
                </svg>
                <div class="nav-right-avatar" v-else @click="clickLogout">
                    <img :src="this.$store.state.user.avatar" alt="" width="100%" height="100%">
                </div>
            </div>
        </nav>
        <div class="section">
            <keep-alive>
                <router-view/>
            </keep-alive>
        </div>
        <my-dialog v-model="dialogShow" :before-close="beforeDialogClose" :confirm-text="dialogConfirmText">
            <div class="login-header"slot="header">
                <svg class="login-logo">
                    <use xlink:href="#icon-login"></use>
                </svg>
                绑定 GitHub 信息
            </div>
            <my-input class="login-input" v-model="usernameValue" :label="usernameLabel" :placeholder="usernamePlaceholder" ref="usernameInputRef"></my-input>
            <my-input class="login-input" type="password" v-model="tokenValue" :label="passwordLabel" :placeholder="passwordPlaceholder" ref="passwordInputRef"></my-input>
        </my-dialog>
        <svg class="button-write" :class="{'button-write-show': JSON.stringify(this.$store.state.user)!=='{}'}" @click="clickWrite">
            <use xlink:href="#icon-write"></use>
        </svg>
        <drawer v-model="drawerShow" :before-close="beforeDrawerClose" :confirm-text="drawerConfirmText">
            <svg class="edit-icon" slot="title">
                <use xlink:href="#icon-write"></use>
            </svg>
            <my-input class="edit-header" v-model="desValue" :label="desLabel" :placeholder="desPlaceholder" ref="desInputRef"></my-input>
            <div class="edit-section">
                <div class="edit-section-left">
                    <img class="edit-avatar" :src="this.$store.state.user.avatar"/>
                </div>
                <div class="edit-section-right">
                    <p class="edit-author">{{this.$store.state.user.username}}</p>
                    <p class="edit-time"></p>
                </div>
            </div>
            <div class="edit-footer">
                <my-textarea :label="detailLabel" v-model="detailValue" :placeholder="detailPlaceholder" ref="detailTextareaRef"></my-textarea>
            </div>
        </drawer>
    </div>
</template>

<script>
    import Dialog from './components/Dialog'
    import Drawer from './components/Drawer'
    import Input from './components/Input'
    import Textarea from './components/Textarea'

  export default {
    components: {
      'my-dialog': Dialog,
      'my-input': Input,
      'my-textarea': Textarea,
        'drawer': Drawer
    },
    data() {
      return {
          dialogShow: false,
          drawerShow: false,
        dialogConfirmText: '绑定',
        usernameLabel: '用户名',
        usernamePlaceholder: '请输入GitHub用户名',
        passwordLabel: 'Token',
        passwordPlaceholder: '请输入有效Token',
        usernameValue: 'Lkkkkkkg',
        tokenValue: '',
          desValue: '',
          detailValue: '',
          desLabel: '标题',
          detailLabel: '内容',
          drawerConfirmText: '发表',
          desPlaceholder: '请输入标题',
          detailPlaceholder: '请输入内容'
      }
    },
    methods: {
      clickLogin() {
        this.dialogShow = true
      },
      clickLogout() {
        this.$store.commit('SET_USER', {})
          alert('已注销');
      },
        clickWrite() {
           this.drawerShow = true
        },
      beforeDialogClose(action,done) {
          if(action === 'on-confirm') {
              if(!(this.usernameValue && this.tokenValue)) {
                  alert('用户名和Token不能为空')
                  done(false);
              }else {
                  this.$ajax(`https://api.github.com/user?access_token=${this.tokenValue}`).then((res)=>{
                      if(res.status === 200) {
                          if(this.usernameValue === res.data.login) {
                              alert('登陆成功');
                              this.$store.commit('SET_USER', {
                                  username: res.data.login,
                                  avatar: res.data.avatar_url
                              })
                              this.$store.commit('SET_TOKEN', this.tokenValue)
                              this.$refs.usernameInputRef.doExit()
                              this.$refs.passwordInputRef.doExit()
                              done()
                          }else {
                              alert('账号与Token不一致');
                              done(false)
                          }
                      }
                  }).catch(()=>{
                      alert('账号与Token不一致');
                      done(false)
                  })
              }
          }else {
              this.$refs.usernameInputRef.doExit()
              this.$refs.passwordInputRef.doExit()
              done()
          }
      },
        beforeDrawerClose(action,done) {
            if(action === 'on-confirm') {
                if(!(this.desValue && this.detailValue)) {
                    alert('标题和内容不能为空');
                    done(false);
                }else {
                    this.$ajax.post(`https://api.github.com/gists?access_token=${this.$store.state.token}`,{
                        "description": this.desValue,
                        "public": true,
                        "files": {
                            "blog": {
                                "content": this.detailValue
                            }
                        }
                    }).then((res)=>{
                        this.$store.commit('ADD_BLOG', res.data)
                        alert('发表成功')
                        this.$refs.desInputRef.doExit()
                        this.$refs.detailTextareaRef.doExit()
                        done();
                    })
                }
            }else {
                done();
            }
        }
    }
  }
</script>

