<template>
    <div class="home" ref="homeRef">
        <loading v-if="loading"></loading>
        <card v-for="blog in this.$store.state.blogs"
              :key="blog.id"
              :avatar="blog.owner.avatar_url"
              :author="blog.owner.login"
              :time="blog.created_at"
              :des="blog.description"
              :comments="blog.comments"
              @click.native="clickCard(blog)"
        ></card>
    </div>
</template>

<script>
  import Card from '../components/Card'
  import Loading from '../components/Loading'

  export default {
    name: 'home',
    components: {
      'card': Card,
      'loading': Loading
    },
    data() {
      return {
        loading: false
      }
    },
    methods: {
      clickCard(blog) {
        this.$router.push({
          name: 'detail',
          query: {
            id: blog.id
          }
        })
      },
      getBlogs(username) {
        if(username) {
          this.loading = true;
          //加个时间戳防止发表博客后刷新页面使用了缓存
          this.$ajax.get(`https://api.github.com/users/${username}/gists?time=${new Date()}`).then((res) => {
              this.$store.commit('SET_BLOGS', res.data)
              this.loading = false;
          })
        }else {
            this.$store.commit('SET_BLOGS', [])
        }
      }
    },
    computed: {
      isLogin() {
          return this.$store.state.user
      },
        isAddBlog() {
            return this.$store.state.blogs
        },
    },
    watch: {
        isLogin(user) {
            //登陆成功后自动请求数据
        this.getBlogs(user.username)
      },
        isAddBlog() {
            this.$refs.homeRef.scrollTop = 0;
        }
    },
    //记住滚动位置
    beforeRouteLeave(to, from, next) {
      this.$store.commit('SET_SCROLLTOPS', {
        key: 'home',
        value: this.$refs.homeRef.scrollTop
      });
      next();
    },
    beforeRouteEnter(to, from, next) {
      next(vm => {
        vm.$refs.homeRef.scrollTop = vm.$store.state.scrollTops.home
      })
    }
  }
</script>

<style lang="scss" scoped>
    .home {
        position: absolute;
        top: 46px;
        left: 0;
        right: 0;
        bottom: 0;

        overflow: auto;
        -webkit-overflow-scrolling: touch;
    }
</style>
